import {JetView} from "webix-jet";
import {getServerBase} from "models/utils";


export default class LogListView extends JetView {
	config(){
		if (webix.isUndefined($$("mailcontent"))){
			webix.ui({
				view: "popup",
				id: "mailcontent",
				hidden: true,
				height: 400, width: 1000,
				body:{
					view: "scrollview",
					id: "mail",
					scroll: "xy", // vertical scrolling
					body: {
						rows:[
							{id:"mailc", template:"<b>fett</b> normal <i>kursiv</i>"}
						]
					}
				}
			}
			);
		}

		return {
			gravity:2,
			rows:[
				{
					cols:[
						{type:"header", id: "logListTitle", template:"Verschickte Mails"},
						{
							view: "button",
							id: "refLog",
							type: "icon",
							icon: "mdi mdi-reload",
							on: {
								onItemClick: function (id) {
									$$("logList").clearAll();
									$$("logList").load(getServerBase() + "/untis/log");
								}
							}
						}
					]

				},
				{
					cols: [
						{
							id:"logList",
							view:"datatable",
							localId:"logList",
							select:false,
							editable:false,
							tooltip:true,
							scroll:"y",
							url: getServerBase()+"/untis/log",
							on:{
								onBeforeLoad:function(){
//									$$("mailcontent").hide();
									this.showOverlay("Log abrufen...");
								},
								onAfterLoad:function(){
									if (this.count() < 1) { // if there are no data items
										this.showOverlay("Kein Log gefunden.");
									}
									else{
										this.hideOverlay();
									}
								},
								onItemClick: function (id) {
									var entr = this.getItem(id);
									$$("mailc").setHTML(entr.mail);
									$$("mailcontent").show(this.getNode(), {pos: top, y: -10});
								},
								onSelectChange: function() {
									var entr = this.getItem(this.getSelectedId(true).join());
									$$("mailc").setHTML(entr.mail);
									$$("mailcontent").show(this.getNode(), {pos: top, y: -10});
								}
								// onAfterSelect: function(selection){
								// 	var entr = this.getItem(selection.id);
								// 	$$("mailc").setHTML(entr.mail);
								// 	return false;
								// }
							},

							scheme:{
								$init:function(obj){		// 2019-01-01T19:38:21.112
									if(obj!= null && "sendDate" in obj && obj.sendDate != null){
										obj.sendDateD = new Date(obj.sendDate);
									}
									if(obj!= null && "text" in obj && obj.text.length > 0){
										obj.sendTxt = obj.send?"send":"test";
										obj.timeTxt = obj.time==="morgen"?"Tag":"Fr.";
									}
								}
							},

							columns:[
								{id:"sendDateD", header:"am", sort:"date", width: 140, format: webix.Date.dateToStr("%d.%m.%Y %H:%i"), tooltip:"Mail verschickt am",adjustBatch: 10},
								{id:"sendTxt", header:"", width: 40, tooltip:"Geplant oder verschickt"},
								{id:"timeTxt", header:"Grund", width: 60, tooltip:"Tagesmail/Freitagsmail"},
								{id:"text", header:"Text", sort:"string", fillspace: true, tooltip:"Mailinhalt"}
							]
						},
					]
				},
			]
		};
	}

	init(){
	}
}
