export var Config;

export function LoadConfig(){
	webix.ajax().sync().get(getServerBase()+"/config", function(text, data, XmlHttpRequest){
		Config = data.json();
	});
}

export function SaveConfig(){
	webix.ajax().sync().headers({
		"Content-type":"application/json"
	}).post(getServerBase()+"/config/json", JSON.stringify(Config));
}


