export function checkDate(field)
{
	var allowBlank = true;
	var minYear = 1902;
	var maxYear = (new Date()).getFullYear()+1;

	var errorMsg = "";
	var regs;

	if(field === "heute"){
		return errorMsg;
	}

	// regular expression to match required date format
	const re = /^(\d{1,2})\.(\d{1,2})\.(\d{4})$/;


	if(field.value != '') {
		if(regs = field.match(re)) {
			if(regs[1] < 1 || regs[1] > 31) {
				errorMsg = "Invalid value for day: " + regs[1];
			} else if(regs[2] < 1 || regs[2] > 12) {
				errorMsg = "Invalid value for month: " + regs[2];
			} else if(regs[3] < minYear || regs[3] > maxYear) {
				errorMsg = "Invalid value for year: " + regs[3] + " - must be between " + minYear + " and " + maxYear;
			}
		} else {
			errorMsg = "Invalid date format: " + field.value;
		}
	} else if(!allowBlank) {
		errorMsg = "Empty date not allowed!";
	}

	return errorMsg;
}

export function updateTitle(zeit, elem){
	var stichtag = Config.mailTag;
	if( stichtag === "heute"){
		stichtag = webix.i18n.dateFormatStr(new Date());
	}
	var stichd = webix.i18n.dateFormatDate(stichtag);
	var a;
	var e;

	var txt = "";
	switch(zeit){
		case "heute": txt += webix.i18n.dateFormatStr(stichd); break;
		case "morgen":
			a = new Date(stichd);
			if(a.getDay() > 0 && a.getDay() < 5){
				a.setDate(a.getDate()+1);
			}
			else{
				a.setDate(a.getDate()+3);
			}
			txt += webix.i18n.dateFormatStr(a);
			break;
		case "woche":
			a = new Date(stichd);
			a.setDate(a.getDate()-a.getDay()+1);
			e = new Date(a);
			e.setDate(e.getDate()+4);
			txt += webix.i18n.dateFormatStr(a) + " - ";
			txt += webix.i18n.dateFormatStr(e);
			break;
		case "kommende":
			a = new Date(stichd);
			a.setDate(a.getDate()-a.getDay()+1+7);
			e = new Date(a);
			e.setDate(e.getDate()+4);
			txt += webix.i18n.dateFormatStr(a) + " - ";
			txt += webix.i18n.dateFormatStr(e);
			break;
		case "neues":
			a = new Date(stichd);
			a.setDate(a.getDate()-a.getDay()+1);
			e = new Date(a);
			e.setDate(e.getDate()+4);
			txt += webix.i18n.dateFormatStr(a) + " - ";
			txt += webix.i18n.dateFormatStr(e) + " nderungen";
			break;
		case "alle":
			a = new Date(stichd);
			a.setDate(a.getDate()-a.getDay()+1);
			txt += webix.i18n.dateFormatStr(a) + " - ";
			txt += " ...";
			break;
		case "all": txt += " Alles"; break;
	}
	$$(elem).define("template", txt);
	$$(elem).refresh();
}