import {JetView} from "webix-jet";
import {getServerBase} from "models/utils";


export default class EPlanView extends JetView {
	config(){

		var format = webix.Date.dateToStr("%d.%m.%Y");

		return {
			view:"tabview",
			id:"eplanview",
			cells: [{}]
		};
	}
	init(view, url) {
		/*
			Ask server for list of bereiche and generate one tab for each bereich
		 */
		webix.ajax().sync().get(getServerBase() + "/eplan/bereiche", function (text, data, XmlHttpRequest) {
			var ber = data.json();
			var parent = $$("eplanview");
			var id = parent.getValue(); //id of active cell

			for (var key in ber) {
				EPlanView.addTab(parent, ber[key]);
			}
			EPlanView.addSTab(parent, "_");

			parent.removeView(id);
		});

		/*
			If there are parameters to this url generate a further tab for individual data
		 */
		if(url.length == 3){
			EPlanView.loadData(url[1].page, url[2].page);
		}
	}

	static loadData(type, val){
		/*
			get data type: ['lehrer', 'klasse', 'raum', 'fach']
		 */
		webix.ajax(getServerBase() + "/eplan/select/" + type + "/" + val, function(text,data){

			var js = data.json();
			for(var i = 0; i < js.length; i++){
				var csss = [
					"anwRow0u",
					"anwRow0g",
					"anwRow1u",
					"anwRow1g"
				];
				var item = js[i];
				var idx = item.lineCode%4;
				item.$css = csss[idx];
				if(typeof item.eplan !== "undefined"){
					for (var key in item.eplan) {
						if (item.eplan.hasOwnProperty(key)) {
							item[key] = item.eplan[key];
						}
					}
					item.xid = item.eplan.id;
				}
			}

			$$("eplanstab").parse(js);

			var tabbar = $$("eplanview").getTabbar();
			var anz = tabbar.config.options.length;
			tabbar.config.options[anz-1].value = val;
			tabbar.render();
			tabbar.setValue("eplanstab");

			webix.ui({
				view:"contextmenu",
				data:["->"],
				click:function(txt, cont){
					if(txt ==="->"){
						var grid = $$("eplanstab");
						var context = this.getContext();
						var id = context.id; //id of the clicked list item

						var type = id.column;
						var val = grid.getItem(id)[type];
						grid.clearAll();
						EPlanView.loadData(type, val);
					}
					return false;
				}
			}).attachTo( $$("eplanstab"));
		});

	}

	static addTab(parent, val){
		var addTab ={
			header: "New Tab",
			body: {
				view: "datatable",
				id: "allgemeintab",
				editable: true,
				tooltip: false,
				scroll: "y",
//					yCount: 20,
				url: getServerBase() + "/eplan",
				save: {url: "json->" + getServerBase() + "/eplan/new", updateFromResponse: true},
				scheme:{
					$change:function(item){
						var csss = [
							"anwRow0u",
							"anwRow0g",
							"anwRow1u",
							"anwRow1g"
						];
						var idx = item.lineCode%4;
						item.$css = csss[idx];
						item.id = item.eplan.id;
						item.bereich = item.eplan.bereich;
						item.bemerkung = item.eplan.bemerkung;
						item.klasse = item.eplan.klasse;
						item.fach = item.eplan.fach;
						item.lehrer = item.eplan.lehrer;
						item.raum = item.eplan.raum;
						item.wstd = item.eplan.wstd;
						item.lgz = item.eplan.lgz;
					}
				},
				columns: [
					{
						id: "klasse",
						header: {text: "Klasse", css: "anwLeft"},
						sort: "text",
						fillspace: 5,
						editor: "text"
					},
					{
						id: "fach",
						header: {text: "Fach", css: "anwLeft"},
						sort: "text",
						fillspace: 5,
						editor: "text"
					},
//						{id:"fak", header: {text: "Fakulta", css:"anwLeft"}, sort:"text",  fillspace: 10, editor: "text"},
					{
						id: "lehrer",
						header: {text: "Lehrer", css: "anwLeft"},
						sort: "text",
						fillspace: 5,
						editor: "text"
					},
					{
						id: "raum",
						header: {text: "Raum", css: "anwLeft"},
						sort: "text",
						fillspace: 5,
						editor: "text"
					},
					{
						id: "wstd",
						header: {text: "Wstd/SJ", css: "anwLeft"},
						sort: "int",
						fillspace: 2,
						editor: "text"
					},
					{id: "lgz", header: {text: "LGZ", css: "anwLeft"}, sort: "int", fillspace: 1, editor: "text"},
					{
						id: "bemerkung",
						header: {text: "Bemerkung", css: "anwLeft"},
						sort: "text",
						fillspace: 10,
						editor: "text"
					},
					{
						id: "edit",
						header: "",
						fillspace: 1,
						css: "editaction",
						template: "<i class=\" mdi mdi-plus\"></i>"
					},
					{id: "trash", header: "", fillspace: 1, css: "trashaction", template: "{common.trashIcon()}"}
				],
				tabelle: "allgemeintab",
				onClick: {
					"editaction": function (e, id, trg) {
						var data = {kuk: "", begin: "", end: "", klasse: "", fach: "", aufgabe: ""};
						var tab = id.column.slice(2);
						var row = $$(tab).getIndexById(id);
						$$(tab).editStop();
						$$(tab).add(data, row);
						$$(tab).showItemByIndex(row + 1);
						return false; // here it blocks the default behavior
					},
					"trashaction": function (e, id, trg) {
						var tab = id.column.slice(2);
						$$(tab).remove(id);
						return false; // here it blocks the default behavior
					}
				}
			}
		};

		var newTab = webix.copy(addTab);
		newTab.header = val;
		newTab.id = "id_" + val;
		newTab.body.url = getServerBase() + "/eplan/bereich/" + val;
		newTab.body.save = {url: "json->" + getServerBase() + "/eplan/bereich/" + val, updateFromResponse: true}
		newTab.body.id = val + "tab";
		newTab.body.columns[7].id = "e_" + val + "tab";
		newTab.body.columns[8].id = "t_" + val + "tab";
		parent.addView(newTab);
		//
		// webix.ui({
		// 	view:"contextmenu",
		// 	data:["->"],
		// 	click:function(txt, cont){
		// 		if(txt ==="->"){
		// 			var grid = $$("eplanstab");
		// 			var context = this.getContext();
		// 			var id = context.id; //id of the clicked list item
		//
		// 			var type = id.column;
		// 			var val = grid.getItem(id)[type];
		// 			grid.clearAll();
		// 			EPlanView.loadData(type, val);
		// 		}
		// 		return false;
		// 	}
		// }).attachTo( $$("id_" + val));

	}

	static addSTab(parent, name){
		var addTab ={
			header: "New Tab",
			body: {
				view: "datatable",
				id: "eplanstab",
				editable: true,
				tooltip: false,
				scroll: "y",
				save: {url: "json->" + getServerBase() + "/eplan/id", updateFromResponse: true},

//			url: getServerBase() + "/eplan/select",
				columns: [
					{
						id: "klasse",
						header: {text: "Klasse", css: "anwLeft"},
						sort: "text",
						fillspace: 5,
						css: "contextaction",
						editor: "text"
					},
					{
						id: "fach",
						header: {text: "Fach", css: "anwLeft"},
						sort: "text",
						fillspace: 5,
						css: "contextaction",
						editor: "text"
					},
//						{id:"fak", header: {text: "Fakulta", css:"anwLeft"}, sort:"text",  fillspace: 10, editor: "text"},
					{
						id: "lehrer",
						header: {text: "Lehrer", css: "anwLeft"},
						sort: "text",
						fillspace: 5,
						css: "contextaction",
						editor: "text"
					},
					{
						id: "raum",
						header: {text: "Raum", css: "anwLeft"},
						sort: "text",
						fillspace: 5,
						css: "contextaction",
						editor: "text"
					},
					{
						id: "wstd",
						header: {text: "Wstd/SJ", css: "anwLeft"},
						sort: "int",
						fillspace: 5,
						editor: "text"
					},
					{id: "lgz", header: {text: "LGZ", css: "anwLeft"}, sort: "int", fillspace: 5, editor: "text"},
					{
						id: "bemerkung",
						header: {text: "Bemerkung", css: "anwLeft"},
						sort: "text",
						fillspace: 10,
						editor: "text"
					},
					{
						id: "edit",
						header: "",
						fillspace: 1,
						css: "editaction",
						template: "<i class=\" mdi mdi-plus\"></i>"
					},
					{id: "trash", header: "", fillspace: 1, css: "trashaction", template: "{common.trashIcon()}"}
				],
				onClick: {
					"editaction": function (e, id, trg) {
						var data = {bereich: "", klasse: "", fach: "", lehrer: "", raum: "", wstd: "", lgz: "", bemerkung: ""};
						var tab = $$("eplanstab");
						var row = tab.getIndexById(id);
						var cdata = tab.getItem(id.row);
						for (var key in data) {
							if (data.hasOwnProperty(key)) {
								data[key] = cdata[key];
							}
						}
						data.lineCode = (cdata.lineCode+1)%2;
						data.bemerkung = "new";
						tab.editStop();
						tab.add(data, row + 1);
						tab.showItemByIndex(row + 1);
						return false; // here it blocks the default behavior
					},
					"trashaction": function (e, id, trg) {
						var tab = $$("eplanstab");
						$$(tab).remove(id);
						return false; // here it blocks the default behavior
					}
				},
				onContext:{} // the empty object
			}
		};

		var newTab = webix.copy(addTab);
		newTab.header = name;
		newTab.id = "id_" + name;
		newTab.body.url = "";
		newTab.body.save = "";
//		newTab.body.id = name + "tab";
		newTab.body.columns[7].id = "e_" + name + "tab";
		newTab.body.columns[8].id = "t_" + name + "tab";
		parent.addView(newTab);

	}

}
