import "styles/app.css";
import {JetApp, HashRouter} from "webix-jet";
import {Config, LoadConfig} from "models/utils";

export default class MyApp extends JetApp{
	constructor(config){
		const defaults = {
			id		: "VMailerUI",
			version : "0.1.1",
			router 	: HashRouter,
			debug 	: !PRODUCTION,
			start 	: "/top/vlist",
			theme	: webix.storage.local.get("theme_qadashboard") || "",
			serverURL: "http://localhost:8086"
		};

		super({ ...defaults, ...config });

		this.attachEvent("app:error:resolve", function(err, url) {
			webix.delay(() => this.show("/top/vlist"));
		});

		this.attachEvent("onAjaxError", function(text, xml, ajax, owner){
			//text - response text
			//xml - response xml object
			//ajax - xmlHttpRequest object
			//owner - component which triggered error

			webix.message({
				text:"AJAX-error: from:" + ajax._links.self.href + " message:" + ajax.message,
				type:"error",
				expire: 10000,
				id:"message1"
			});
		});

		this.attachEvent("onLoadError", function(text, xml, ajax, owner){
			//text - response text
			//xml - response xml object
			//ajax - xmlHttpRequest object
			//owner - component which triggered error

			webix.message({
				text:"AJAX-error: from:" + ajax._links.self.href + " message:" + ajax.message,
				type:"error",
				expire: 10000,
				id:"message1"
			});
		});

		webix.i18n.setLocale("de-DE");

		// this.attachEvent("app:render", function(view, url, result){
		// 	var mailBetreff = Config.mailBetreff;
		// 	console.log("rendering: " + view + "mailBetreff aus Config: " + mailBetreff);
		// })
		//
		// Config.load(url, "json", function(text, data, http_request){
		// 	console.log("received config: " + data);
		// });
		// Config.load(function(){
		// 	return webix.ajax().get(url, function(text, xml, xhr){
		// 		var obj = JSON.parse(text);
		// 		return obj;
		// 	});
		// });
		LoadConfig();
	}
}

if (!BUILD_AS_MODULE){
	webix.ready(() => {
		if (!webix.env.touch && webix.env.scrollSize && webix.CustomScroll)
			webix.CustomScroll.init();
		new MyApp().render();
	});
}

