import {JetView, plugins} from "webix-jet";
import {getServerBase} from "../models/utils";

export default class MenuView extends JetView {
	config(){
		const theme = this.app.config.theme;
		var cfg = {
			width:150,
			localId:"side:menu",
			id: "SideMenu",
			view:"sidebar",
			css:theme,
			collapsed:true,
			data:[]
		};
		var sMenu = [
			{ id:"vlist", value:"Vertretungsmail", icon:"mdi mdi-teach" },
//			{ id:"AnwesenPaarView", value:"Anwesenheit Paare", icon:"mdi mdi-chair-school" },
			{ id:"AnwesenView", value:"Anwesenheit", icon:"mdi mdi-table-eye" },
			{ id:"AnwesenUserView", value:"Pers. Anwesenheit", icon:"mdi mdi-table-edit" },
//			{ id:"AnwesenSView", value:"Anwesenheit Split", icon:"mdi mdi-timetable" },
			{ id:"AnwesenAngaben", value:"Angaben zur Anwesenheit", icon:"mdi mdi-view-list" },
//				{ id:"chart", value:"Grafik", icon:"mdi mdi-table-edit" },
			{ id:"logListView", value:"Maillog", icon:"mdi mdi-email-send-outline" },
			{ id:"AnalyseView", value:"Analyse", icon:"mdi mdi-chart-areaspline" },
			{ id:"configView", value:"Konfiguration", icon:"mdi mdi-settings" }
		];
		var lMenu = [
			{ id:"vlist", value:"Vertretungsmail", icon:"mdi mdi-teach" },
//			{ id:"AnwesenPaarView", value:"Anwesenheit Paare", icon:"mdi mdi-chair-school" },
			{ id:"AnwesenView", value:"Anwesenheit", icon:"mdi mdi-table-eye" },
			{ id:"AnwesenUserView", value:"Pers. Anwesenheit", icon:"mdi mdi-table-edit" },
//			{ id:"AnwesenSView", value:"Anwesenheit Split", icon:"mdi mdi-timetable" },
			{ id:"AnwesenAngaben", value:"Angaben zur Anwesenheit", icon:"mdi mdi-view-list" },
//				{ id:"chart", value:"Grafik", icon:"mdi mdi-table-edit" },
			{ id:"EplanView", value:"EPlan", icon:"mdi mdi-school" },
			{ id:"EplanSumView", value:"ESPlan", icon:"mdi mdi-sigma" },
			{ id:"AnrechnungView", value:"Anr", icon:"mdi mdi-account-clock-outline" },
			{ id:"logListView", value:"Maillog", icon:"mdi mdi-email-send-outline" },
			{ id:"AnalyseView", value:"Analyse", icon:"mdi mdi-chart-areaspline" },
			{ id:"configView", value:"Konfiguration", icon:"mdi mdi-settings" }
		];
		webix.ajax().sync().get(getServerBase() + "/config", function (text, data) {

			var js = data.json();

			if(js["eplan"] === "true"){
				cfg.data = lMenu;
			}
			else{
				cfg.data = sMenu;
			}
		});
		return cfg;
	}

	init(sidebar) {
		this.use(plugins.Menu, this.$$("side:menu"));
		this.on(this.app, "menu:toggle", () => this.$$("side:menu").toggle());
		sidebar.getPopup().attachEvent("onBeforeShow", () => false);
	}
}
