import {JetView} from "webix-jet";
// import {d3} from "public/d3";
import {getServerBase} from "models/utils";

export default class HistogramView extends JetView {
	config() {
		return {
			view:"d3-chart",
			id: "histogramView",
			url: getServerBase()+"/analyse/aggList/week",
			ready: function() {
				const myself = this;
				var barColor = "steelblue";

				function segColor(c) {
					return [
						"#A85414", "#BB7116", "#D68C16", "#F3A616",
						"#3C793C", "#3B8D3B", "#3DB13D", "#3ED93E"
					][c];
				}

				function segName(c) {
					return [
						"- Klasse im Praktikum", "- Klasse abwesend", "- KuK abwesend", "- KuK dienstl. abwesend",
						"+ Klasse im Praktikum", "+ Klasse abwesend", "+ KuK abwesend", "+ KuK dienstl. abwesend"
					][c];
				}


				// var svg = d3.select(view).append("svg")
				// 	.attr("width",width).attr("height", height);
// http://bl.ocks.org/NPashaP/96447623ef4d342ee09b
				webix.ajax().sync().get(getServerBase() + "/analyse/aggList/week", function (text, data, XmlHttpRequest) {
					const width = myself.$width;
					const height = myself.$height;
					this.vwHistogram = myself.$view;
					const jData = data.json().data;
					const hWidth = width;
					const hHeight = height / 2;
					const pWidth = width / 2;

					var pieDim = {w: pWidth, h: hHeight};
					var hGDim = {t: 60, r: 0, b: 60, l: 10};
					hGDim.w = hWidth - hGDim.l - hGDim.r;
					hGDim.h = hHeight - hGDim.t - hGDim.b;

					function dashboard(id, fData) {
						// compute total for each week.
						fData.forEach(function (d) {
							d.total = d.mehr[0] + d.mehr[1] + d.mehr[2] + d.mehr[3]
								+ d.weniger[0] + d.weniger[1] + d.weniger[2] + d.weniger[3];
							d.cnts = new Array(8);
							d.cnts[0] = d.weniger[0];
							d.cnts[1] = d.weniger[1];
							d.cnts[2] = d.weniger[2];
							d.cnts[3] = d.weniger[3];
							d.cnts[4] = d.mehr[0];
							d.cnts[5] = d.mehr[1];
							d.cnts[6] = d.mehr[2];
							d.cnts[7] = d.mehr[3];
						});

						// function to handle histogram.
						function histoGram(fD) {
							var hG = {};

							//create svg for histogram.
							var hGsvg = d3.select(id).append("svg")
								.attr("width", hGDim.w + hGDim.l + hGDim.r)
								.attr("height", hGDim.h + hGDim.t + hGDim.b).append("g")
								.attr("transform", "translate(" + hGDim.l + "," + hGDim.t + ")");

							// create function for x-axis mapping.
							var x = d3.scaleBand()
								.domain(fD.map(function (d) {
									return d[0];
								}))
								.rangeRound([0, hGDim.w])
								.padding(0.1);

							// Add x-axis to the histogram svg.
							hGsvg.append("g").attr("class", "x axis")
								.attr("transform", "translate(0," + hGDim.h + ")")
								.call(d3.axisBottom(x))
								.selectAll("text")
								.style("text-anchor", "end")
								.attr("dx", "-.8em")
								.attr("dy", ".15em")
								.attr("transform", function (d) {
									return "rotate(-90)";
								});
//								.call(d3.svg.axis().scale(x).orient("bottom"));

							// Create function for y-axis map.
							var y = d3.scaleLinear().range([hGDim.h, 0])
								.domain([0, d3.max(fD, function (d) {
									return d[1];
								})]);

							// Create bars for histogram to contain rectangles and freq labels.
							var bars = hGsvg.selectAll(".bar").data(fD).enter()
								.append("g").attr("class", "bar");

							//create the rectangles.
							bars.append("rect")
								.attr("x", function (d) {
									return x(d[0]);
								})
								.attr("y", function (d) {
									return y(d[1]);
								})
								.attr("width", x.bandwidth())
								//										.attr("width", hGDim.w/fD.length)
								.attr("height", function (d) {
									return hGDim.h - y(d[1]);
								})
								.attr('fill', barColor)
								.on("mouseover", mouseover)// mouseover is defined below.
								.on("mouseout", mouseout);// mouseout is defined below.

							//Create the frequency labels above the rectangles.
							bars.append("text").text(function (d) {
								return d3.format(",")(d[1])
							})
								.attr("x", function (d) {
									return x(d[0]);
								}) //+x.bandwidth()/2; })
								.attr("y", function (d) {
									return y(d[1]) - 5;
								})
								.attr("text-anchor", "middle");

							function mouseover(d) {  // utility function to be called on mouseover.
								// filter for selected state.
								var st = fData.filter(function (s) {
										return s.startOfWeek == d[0];
									})[0],
									nD = d3.keys(st.cnts).map(function (s) {
										return {type: s, cnts: st.cnts[s]};
									});

								// call update functions of pie-chart and legend.
								pC.update(nD);
								leg.update(nD);
							}

							function mouseout(d) {    // utility function to be called on mouseout.
								// reset the pie-chart and legend.
								pC.update(tF);
								leg.update(tF);
							}

							// create function to update the bars. This will be used by pie-chart.
							hG.update = function (nD, color) {
								// update the domain of the y-axis map to reflect change in frequencies.
								y.domain([0, d3.max(nD, function (d) {
									return d[1];
								})]);

								// Attach the new data to the bars.
								var bars = hGsvg.selectAll(".bar").data(nD);

								// transition the height and color of rectangles.
								bars.select("rect").transition().duration(500)
									.attr("y", function (d) {
										return y(d[1]);
									})
									.attr("height", function (d) {
										return hGDim.h - y(d[1]);
									})
									.attr("fill", color);

								// transition the frequency labels location and change value.
								bars.select("text").transition().duration(500)
									.text(function (d) {
										return d3.format(",")(d[1])
									})
									.attr("y", function (d) {
										return y(d[1]) - 5;
									});

							}
							return hG;
						}

						// function to handle pieChart.
						function pieChart(pD) {
							var pC = {};
							pieDim.r = Math.min(pieDim.w, pieDim.h) / 2;

							// create svg for pie chart.
							var piesvg = d3.select(id).append("svg")
								.attr("width", pieDim.w).attr("height", pieDim.h).append("g")
								.attr("transform", "translate(" + pieDim.w / 2 + "," + pieDim.h / 2 + ")");

							// create function to draw the arcs of the pie slices.
							var arc = d3.arc().outerRadius(pieDim.r - 10).innerRadius(0);

							// create a function to compute the pie slice angles.
							var pie = d3.pie().sort(null).value(function (d) {
								return d.cnts;
							});

							// Draw the pie slices.
							piesvg.selectAll("path").data(pie(pD)).enter().append("path").attr("d", arc)
								.each(function (d) {
									this._current = d;
								})
								.style("fill", function (d) {
									return segColor(d.data.type);
								})
								.on("mouseover", mouseover).on("mouseout", mouseout);

							// create function to update pie-chart. This will be used by histogram.
							pC.update = function (nD) {
								piesvg.selectAll("path").data(pie(nD)).transition().duration(500)
									.attrTween("d", arcTween);
							}

							// Utility function to be called on mouseover a pie slice.
							function mouseover(d) {
								// call the update function of histogram with new data.
								hG.update(fData.map(function (v) {
									return [v.startOfWeek, v.cnts[d.data.type]];
								}), segColor(d.data.type));
							}

							//Utility function to be called on mouseout a pie slice.
							function mouseout(d) {
								// call the update function of histogram with all data.
								hG.update(fData.map(function (v) {
									return [v.startOfWeek, v.total];
								}), barColor);
							}

							// Animating the pie-slice requiring a custom function which specifies
							// how the intermediate paths should be drawn.
							function arcTween(a) {
								var i = d3.interpolate(this._current, a);
								this._current = i(0);
								return function (t) {
									return arc(i(t));
								};
							}

							return pC;
						}

						// function to handle legend.
						function legend(lD) {
							var leg = {};

							// create table for legend.
							var legend = d3.select(id).append("table").attr('class', 'legend')
								.attr("width", pWidth).attr("height", hHeight).append("g")
								.attr("transform", "translate(" + pWidth + "," + hHeight + ")");


							// create one row per segment.
							var tr = legend.append("tbody").selectAll("tr").data(lD).enter().append("tr");

							// create the first column for each segment.
							tr.append("td").append("svg").attr("width", '16').attr("height", '16').append("rect")
								.attr("width", '16').attr("height", '16')
								.attr("fill", function (d) {
									return segColor(d.type);
								});

							// create the second column for each segment.
							tr.append("td").text(function (d) {
								return segName(d.type);
							});

							// create the third column for each segment.
							tr.append("td").attr("class", 'legendCounts')
								.text(function (d) {
									return d3.format(",")(d.cnts);
								});

							// create the fourth column for each segment.
							tr.append("td").attr("class", 'legendPerc')
								.text(function (d) {
									return getLegend(d, lD);
								});

							// Utility function to be used to update the legend.
							leg.update = function (nD) {
								// update the data attached to the row elements.
								var l = legend.select("tbody").selectAll("tr").data(nD);

								// update the frequencies.
								l.select(".legendCounts").text(function (d) {
									return d3.format(",")(d.cnts);
								});

								// update the percentage column.
								l.select(".legendPerc").text(function (d) {
									return getLegend(d, nD);
								});
							}

							function getLegend(d, aD) { // Utility function to compute percentage.
								return d3.format("3.2%")(d.cnts / d3.sum(aD.map(function (v) {
									return v.cnts;
								})));
							}

							return leg;
						}

						// calculate total frequency by segment for all state.
						var tF = [0, 1, 2, 3, 4, 5, 6, 7].map(function (d) {
							return {
								type: d, cnts: d3.sum(jData.map(function (t) {
									return t.cnts[d];
								}))
							};
						});

						// calculate total frequency by state for all segment.
						var sF = jData.map(function (d) {
							return [d.startOfWeek, d.total];
						});

						var hG = histoGram(sF); // create the histogram.
						var pC = pieChart(tF); // create the pie-chart.
						var leg = legend(tF);  // create the legend.
					}

					dashboard($$("histogramView").$view, jData);

				});
			}
		};
	}
	init() {
	}
}