import {JetView} from "webix-jet";
import {getServerBase} from "models/utils";


export default class AListSimple extends JetView {
	config(){
		if (webix.isUndefined($$("anaContent"))){
			webix.ui({
				view: "popup",
				id: "anaContent",
				hidden: true,
				height: 400, width: 1000,
				body:{
					view: "scrollview",
					id: "anaB",
					scroll: "xy", // vertical scrolling
					body: {
						rows:[
							{id:"anac", template:"<b>fett</b> normal <i>kursiv</i>"}
						]
					}
				}
			}
			);
		}
		// uploader initialization
		if (webix.isUndefined($$("ananUpload"))) {
			webix.ui({
				view: "uploader",
				id: "ananUpload",
				apiOnly: true,
				multiple: false,
				upload: getServerBase() + "/analyse/gpn",
				on: {
					onBeforeFileAdd: function (item) {
						var type = item.type.toLowerCase(); //deriving file extension
						if (type != "gpn") { //checking the format
							webix.message("Nur Untis-GPN-Dateien!");
							return false;
						}
					},
					onUploadComplete: function(response){
						$$("anaistSimple").load(getServerBase() + "/analyse/slist");
					},
				}
			});
		}



		return {
			gravity:2,
			cols: [
				{
					id:"anaistSimple",
					view:"datatable",
					localId:"anaistSimple",
					select:false,
					editable:false,
					tooltip:true,
					scroll:"y",
					url: getServerBase()+"/analyse/slist",
					on:{
						onBeforeLoad:function(){
							this.showOverlay("Log abrufen...");
						},
						onAfterLoad:function(){
							if (this.count() < 1) { // if there are no data items
								this.showOverlay("Kein Log gefunden.");
							}
							else{
								this.hideOverlay();
							}
						},
					},

					scheme:{
						$init:function(obj){		// 2019-01-01T19:38:21.112
							if(obj!= null && "sendDate" in obj && obj.sendDate != null){
								obj.sendDateD = new Date(obj.sendDate);
							}
						}
					},

					columns:[
						{id:"startOfWeek", header:"Woche", sort:"date", width: 140, format: webix.Date.dateToStr("%d.%m.%Y %H:%i"), tooltip:"Woche",adjustBatch: 10},
						{id:"lehrer", header:"KuK", sort:"string", width: 40, tooltip:"abwesender KuK"},
						{id:"grund", header:"Grund", sort:"string", width: 60, tooltip:"Absenzgrund"},
						{id:"art", header:"Art", sort:"string", fillspace: true, tooltip:"Vertretungsart"},
						{id:"code", header:"Code", fillspace: true, tooltip:"Vertretungscode"},
						{id:"cnt", header:"Anzahl", fillspace: true, tooltip:"Anzahl Vertretungen"}
					]
				},
			]
		};
	}

	init(){

	}
}
