import {JetView} from "webix-jet";
import {getServerBase} from "models/utils";


export default class AnwesenAngaben extends JetView {
	config(){
		return {
			view:"tabview",
			id: "aufgabenacc",
			cells: [
				{
					header: "Klassen-Absenzen (Praktika, ...)",
					id: "absenz",
					body:
						{
							view: "datatable",
							id: "absenztab",
							editable: false,
							tooltip: false,
							scroll: "y",
							url: getServerBase() + "/untis/absenz",

							columns: [
								{
									id: "beginn",
									map: "(date)",
									header: [{text: "Begin", css: "anwLeft"},{ content:"dateRangeFilter"}],
									sort: "date",
									fillspace: 3,
								},
								{
									id: "ende",
									map: "(date)",
									header: [{text: "Ende", css: "anwLeft"},{ content:"dateRangeFilter"}],
									sort: "date",
									fillspace: 3,
								},
								{
									id: "name",
									header: [{text: "Klasse", css: "anwLeft"},{ content:"selectFilter" }],
									sort: "text",
									fillspace: 3,
								},
								{
									id: "grund",
									header: [{text: "Grund", css: "anwLeft"}, { content:"selectFilter" }],
									sort: "text",
									fillspace: 2,
								},
								{
									id: "text",
									header: {text: "Text", css: "anwLeft"},
									sort: "text",
									fillspace: 15,
								}
							]
						}
				},
			]
		};
	}
	init() {
		var addAcc =
			{
				header: "Allgemein",
				body:
					{
						view: "datatable",
						id:"allgemeintab",
						editable:true,
						tooltip:false,
						scroll:"y",
						url: getServerBase()+"/untis/aufgaben/SLZ",
						save: {url: "json->" + getServerBase() + "/untis/aufgaben/SLZ", updateFromResponse: true},
						columns:[
							{id:"kuk", header: {text: "KuK", css:"anwLeft"}, sort:"text",  fillspace: 3, editor: "text"},
							{id:"begin", map: "(date)", header: {text: "Begin", css:"anwLeft"}, sort:"date",  fillspace: 5, editor: "date"},
							{id:"end", map: "(date)", header: {text: "Ende", css:"anwLeft"}, sort:"date",  fillspace: 5, editor: "date"},
							{id:"klasse", header: {text: "Klasse", css:"anwLeft"}, sort:"text",  fillspace: 3, editor: "text"},
							{id:"fach", header: {text: "Fach", css:"anwLeft"}, sort:"text",  fillspace: 3, editor: "text"},
							{id:"aufgabe", header: {text: "Aufgabe", css:"anwLeft"}, sort:"int",  fillspace: 1, editor: "text"},
							{id:"bemerkung", header: {text: "Bemerkung", css:"anwLeft"}, sort:"text",  fillspace: 15, editor: "text"},
							{ id:"edit", header:"", fillspace: 1, css: "editaction", template:"<i class=\" mdi mdi-plus\"></i>"},
							{ id:"trash", header:"", fillspace: 1, css: "trashaction", template:"{common.trashIcon()}"}
						],
						tabelle: "allgemeintab",
						onClick:{
							"editaction" : function(e, id, trg){
								var data = {kuk: "", begin: "", end: "", klasse: "", fach: "", aufgabe: "", bemerkung: ""};
								var tab = id.column.slice(2);
								var row = $$(tab).getIndexById(id);
								$$(tab).editStop();
								$$(tab).add(data,row);
								$$(tab).showItemByIndex(row+1);
								return false; // here it blocks the default behavior
							},
							"trashaction" : function(e, id, trg){
								var tab = id.column.slice(2);
								$$(tab).remove(id);
								return false; // here it blocks the default behavior
							}
						}
					}
			};

		var aufgaben = "";
		webix.ajax().sync().get(getServerBase()+"/untis/aufgaben", function(text, data, XmlHttpRequest){
			aufgaben = data.json();
			var parent = $$("aufgabenacc");
			for (var key in aufgaben) {
				var txt = aufgaben[key];
				var newAcc = webix.copy(addAcc);

				newAcc.header = txt;
				newAcc.id = "id_" + key;
				newAcc.body.url = getServerBase()+"/untis/aufgaben/" + key;
				newAcc.body.save = {url: "json->" + getServerBase() + "/untis/aufgaben/" + key, updateFromResponse: true}
				newAcc.body.id = key + "tab";
				newAcc.body.tabelle = key + "tab";
				newAcc.body.columns[7].id = "e_" + key + "tab";
				newAcc.body.columns[8].id = "t_" + key + "tab";
				parent.addView(newAcc);
			}
		});
	}
}
