import {JetView} from "webix-jet";
// import {d3} from "public/d3";
import {getServerBase} from "models/utils";

export default class Chart extends JetView {

	config(){
		return {
			id: "chart",
			rows:[
				{
					type: "clean",
					cols:
						[
							{type: "header", template: "Konfiguration"},
							{
								type: "header",
								view: "template",
								id: "errormsg",
								css: "important",
								data: {src: ""},
								template: function(obj){
									return obj.src;
								}
							}
						]
				},
				{
					view:"toolbar",
					elements:[
						{ view:"combo", id: "nodes", label: "Knoten:", value:"1",
							options:[
								{id:1, value:"Lehrer"},
								{id:2, value:"Bereich"},
								{id:3, value:"Klasse"},
								{id:4, value:"Fach"}
							]
						},
						{ view:"combo", id: "filter", label: "Filter:", value:"1",
							options:[
								{id:1, value:"Alle"},
								{id:2, value:"Lehrer"},
								{id:3, value:"Bereich"},
								{id:4, value:"Klasse"},
								{id:5, value:"Fach"}
							]
						},
						{
							view: "text", id: "filtertxt", value: "", inputAlign:"left"
						},
						{ view:"combo", id: "kanten", label: "Kanten:", value:"3",
							options:[
								{id:1, value:"Lehrer"},
								{id:2, value:"Bereich"},
								{id:3, value:"Klasse"},
								{id:4, value:"Fach"}
							]
						},
					],
					elementsConfig:{
						on:{
							onchange:function(newv, oldv){
								$$("chart").refresh();
							}
						}
					}
				},
				{
					view:"d3-chart",
					id: "d3chart",
					url: getServerBase()+"/untis/eplan/node/lehrer/Alle/",
					drag: true,
					ready: function(){
						var width = this.$width;
						var height = this.$height;

						var knoten = $$("nodes").getInputNode().value;
						var filter = $$("filter").getInputNode().value;
						var filtertxt = $$("filtertxt").getValue();
						var kanten = $$("kanten").getInputNode().value;

						// knoten = "lehrer";
						// filter = "bereich";
						// filtertxt = "HEG";
						// kanten = "klasse";

//						d3.select(this.$view).remove("svg");

						var svg = d3.select(this.$view).append("svg")
							.attr("width",width).attr("height", height);

						// const simulation = d3.forceSimulation(nodes)
						// 	.force("link", d3.forceLink(links).id(d => d.id))
						// 	.force("charge", d3.forceManyBody(-200))
						// 	.force("collision", d3.forceCollide())
						// 	//									.force("x", d3.forceX())
						// 	//									.force("y", d3.forceY())
						// 	.force("center", d3.forceCenter(width / 2, height / 2))
						// ;
						var simulation = d3.forceSimulation()
							.force("link", d3.forceLink().id(function(d) { return d.id; }))
							.force("charge", d3.forceManyBody())
							.force("center", d3.forceCenter(width / 2, height / 2));

						webix.ajax().sync().get(getServerBase()+"/untis/eplan/node/"+knoten+"/"+filter+"/"+filtertxt,function(text, data, XmlHttpRequest) {
							const nodes = data.json().data;

							webix.ajax().sync().get(getServerBase() + "/untis/eplan/link/"+knoten+"/"+kanten+"/"+filter+"/"+filtertxt, function (text, data, XmlHttpRequest) {
								const links = data.json().data;

								var link = svg.append("g")
									.attr("stroke", "#aaa")
									.attr("stroke-opacity", 0.6)
									.selectAll("line")
									.data(links)
									.enter().append("line");

								var node = svg.append("g")
									.attr("stroke", "#fff")
									.attr("stroke-width", 1.5)
									.selectAll("circle")
									.data(nodes)
									.enter().append("circle")
									.attr("r", 2.5)
									.call(d3.drag()
										.on("start", dragstarted)
										.on("drag", dragged)
										.on("end", dragended));

								node.append("title")
									.text(function(d) { return d.id; });

								simulation
									.nodes(nodes)
									.on("tick", ticked);

								simulation.force("link")
									.links(links);

								function ticked() {
									link
										.attr("x1", function(d) { return d.source.x; })
										.attr("y1", function(d) { return d.source.y; })
										.attr("x2", function(d) { return d.target.x; })
										.attr("y2", function(d) { return d.target.y; });

									node
										.attr("cx", function(d) { return d.x; })
										.attr("cy", function(d) { return d.y; });
								}
							});

							function dragstarted(d) {
								if (!d3.event.active) simulation.alphaTarget(0.3).restart();
								d.fx = d.x;
								d.fy = d.y;
							}

							function dragged(d) {
								d.fx = d3.event.x;
								d.fy = d3.event.y;
							}

							function dragended(d) {
								if (!d3.event.active) simulation.alphaTarget(0);
								d.fx = null;
								d.fy = null;
							}
						});

					}
				}
			]
		};
	}
	init() {
	}

}
