import {JetView} from "webix-jet";
import {getServerBase} from "models/utils";


export default class EPlanSumView extends JetView {
	config() {

		var format = webix.Date.dateToStr("%d.%m.%Y");

		return {
			view: "datatable",
			id: "eplansumtab",
			resizeRow: false,
			editable: false,
			tooltip: false,
			select: "row",
			hover: "myhover",
			css: "rows webix_header_border webix_data_border",
			url: getServerBase() + "/eplan/summen",
			scheme: {
				//     class EPlanSummen{
				//         String lehrer;
				//         Map<String,Double> bereichsSummen;
				//         Double gesamt;
				//         Double soll;
				//         Double anrechnungen;
				$change: function (item) {
					var csss = [
						"anwRow0u",
						"anwRow0g",
						"anwRow1u",
						"anwRow1g"
					];
					//						item.$css = csss[0];
					item.vertragssoll = parseFloat(item.soll);
					item.anrechnungen = parseFloat(item.anrechnungen);
					item.gesamt = parseFloat(item.gesamt);
					item.wochensoll = item.vertragssoll - item.anrechnungen;
					item.delta = item.gesamt - item.wochensoll;
					var bsum = item.bereichsSummen;
					for (var ber in bsum) {
						if (bsum.hasOwnProperty(ber)) {
							item["idsum_" + ber] = parseFloat(bsum[ber]);
						}
					}
				}
			},
			columns: [	// header
				{
					id: "lehrer", header: {text: "Lehrer", css: "anwColHeaderLeft"},
					sort: "string",
					fillspace: 1
				},
				{
					id: "vorname", header: {text: "Vorname", css: "anwColHeaderLeft"},
					sort: "string",
					fillspace: 3
				},
				{
					id: "nachname",
					header: {text: "Nachname", css: "anwColHeaderLeft"},
					sort: "string",
					fillspace: 3
				},
				{
					id: "vertragssoll",
					header: {text: "Vertragssoll", css: "anwColHeader"},
					sort: "int",
					fillspace: 1,
					css:{"text-align":"center"}
				},
				{
					id: "anrechungen",
					header: {text: "Anrechnung", css: "anwColHeader"},
					sort: "int",
					fillspace: 1,
					css:{"text-align":"center"}

				},
				{
					id: "wochensoll",
					header: {text: "Wochensoll", css: "anwColHeader"},
					sort: "int",
					fillspace: 1,
					css:{"text-align":"center"}

				},
				{
					id: "gesamt",
					header: {text: "Ist", css: "anwColHeader"},
					sort: "int",
					fillspace: 1,
					css:{"text-align":"center"}

				},
				{
					id: "delta",
					header: {text: "Ist-Soll", css: "anwColHeader"},
					sort: "int",
					fillspace: 1,
					css:{"text-align":"center"}

				},
			]
		};
	}

	init() {
		var addTab =
			{
				id: "klasse",
				header: {text: "Klasse", css: "anwColHeader"},
				sort: "int",
				fillspace: 1,
				css:{"text-align":"center"}

			};

		webix.ajax().sync().get(getServerBase() + "/eplan/bereiche", function (text, data, XmlHttpRequest) {
			var ber = data.json();
			var parent = $$("eplansumtab");
			var col = 8;
			for (var key in ber) {
				var newTab = webix.copy(addTab);
				var val = ber[key];
				newTab.header.text = val;
				newTab.id = "idsum_" + val;
				newTab.css = col%2==0?"col0":"col1";
				parent.config.columns[col++] = newTab;
			}
			parent.refreshColumns();
		});

		$$("eplansumtab").attachEvent("onItemClick",function(id, e){
			var grid = $$("eplansumtab");
			var cdata = grid.getItem(id.row);
			var val = cdata.lehrer;

//			this.$scope.app.show("/top/EplanSView/lehrer/" + val);
			this.$scope.app.show("/top/EplanView/lehrer/" + val);
//			$$("eplanview").getTabbar().setValue("id_TEST");
		});
	}
}
