import {JetView} from "webix-jet";
import {getServerBase} from "models/utils";


export default class AnrechnungView extends JetView {
	config(){

		var format = webix.Date.dateToStr("%d.%m.%Y");

		return {
			view: "datatable",
			id: "anrechnungtab",
			editable: false,
			tooltip: false,
			scroll: "y",
			save: {url: "json->" + getServerBase() + "/anrechnung/id", updateFromResponse: true},

//			url: getServerBase() + "/eplan/select",
			columns: [
				{
					id: "anrnummer",
					header: {text: "Nr", css: "anwLeft"},
					sort: "int",
					fillspace: 2
				},
				{
					id: "lehrer",
					header: {text: "Lehrer", css: "anwLeft"},
					sort: "text",
					fillspace: 5,
					css: "contextaction"
				},
				{
					id: "grund",
					header: {text: "Grund", css: "anwLeft"},
					sort: "text",
					fillspace: 5,
					css: "contextaction"
				},
				{
					id: "wwert",
					header: {text: "Wstd/SJ", css: "anwLeft"},
					sort: "int",
					fillspace: 5
				},
				{
					id: "beginnD",
					header: {text: "Beginn", css: "anwLeft"},
					sort: "date",
					fillspace: 4,
					format:format
				},
				{
					id: "endeD",
					header: {text: "Ende", css: "anwLeft"},
					sort: "date",
					fillspace: 4,
					format:format
				},
				{
					id: "text",
					header: {text: "Kommentar", css: "anwLeft"},
					sort: "text",
					fillspace: 15,
					css: "contextaction"
				}
			],
			scheme:{
				$init:function(obj){
					var d = new Date(obj.beginn);
					obj.beginnD = isNaN(d) ? "" :  d;

					d = new Date(obj.ende);
					obj.endeD = isNaN(d) ? "" :  d;
				}
			}
		};
	}
	init(view, url) {
		AnrechnungView.loadData();
	}

	static loadData(type, val){
		webix.ajax(getServerBase() + "/untis/anrechnung", function(text,data){

			var js = data.json().data;
			var idx = 1;
			var old = "";
			for(var i = 0; i < js.length; i++){
				var csss = [
					"anwRow0u",
					"anwRow0g",
					"anwRow1u",
					"anwRow1g"
				];
				var item = js[i];
				if(!(old === item.lehrer)){
					idx++;
					idx %= 2;
					old = item.lehrer;
				}
				item.$css = csss[idx];
			}

			$$("anrechnungtab").parse(js);
		});

	}
}
