import {JetView} from "webix-jet";
import MenuView from "views/menu";
import NotificationsView from "views/notifications";
import {getServerBase} from "../models/utils";

export default class TopView extends JetView {

	config(){
		const theme = this.app.config.theme;
		return {
			rows:[
				{
					view:"toolbar",
					css:theme,
					height:56,
					elements:[
						{
							paddingY:7,
							rows:[
								{
									cols:[
										{
											view:"icon",
											icon:"mdi mdi-menu",
											click:() => this.app.callEvent("menu:toggle")
										},
										{
											view:"label", template:"BKEST Zentrale"
										},
										{},
										{
											view:"icon",
											icon:"mdi mdi-bell",
											badge:0,
											localId:"bell",
											tooltip:"Open latest notifications",
											click:function(){
												this.$scope.notifications.showWin(this.$view);
											}
										},
										{width:8},
										{
											view:"icon",
											localId:"themes",
											icon:"mdi mdi-invert-colors",
											color:theme,
											click:function(){
												let color = this.config.color;
												color = !color ? "webix_dark" : "";
												webix.storage.local.put("theme_qadashboard",color);
												this.$scope.app.config.theme = color;
												this.$scope.app.refresh();
											}
										}
									]
								}
							]
						},
						{ width:6 }
					]
				},
				{
					cols:[
						MenuView,
						{ $subview:true }
					]
				},
				{
					view:"toolbar",
					height:35,
					css: "statusLine",
					elements:[
						{
							rows:[
								{
									cols:[
										{
											view:"label", id: "lblLoaded", template:"Daten von: ",
										},
										{},
										{width:8},
										{ view: "icon", icon: "mdi mdi-18px mdi-copyright"},
										{ view:"label", adjust: "right", template:"BK Ernhrung, Sozialwesen, Technik in Geilenkirchen",
										},
									]
								}
							]
						},
						{ width:6 }
					]
				},

			]
		};
	}
	init(){
		this.notifications = this.ui(NotificationsView);

		const theme = this.app.config.theme;
		this.$$("themes").config.tooltip = theme ? "Come back to the light side of the Force" : "Come to the dark side";
		this.$$("themes").refresh();

		var loadLoaded = function(){
			webix.ajax().get(getServerBase()+"/untis/statistik", function(text, data, XmlHttpRequest) {
				var format = webix.Date.dateToStr("%d.%m.%Y %H:%i");		// 2019-01-17T20:56:36.053
				var vs = data.json().version;
				var lbl =  "";
				if( typeof data.json().lastLoaded === "undefined"){
					lbl = "Daten werden geladen ...";
				}
				else{
					lbl =  "Daten von " + format(new Date(data.json().lastLoaded));
				}
				$$("lblLoaded").define("template", "["+vs+"] " + lbl);
				$$("lblLoaded").refresh();
			});
		};
		setInterval(loadLoaded, 1000);

		loadLoaded();

		// this.on(this.app,"read:notifications",() => {
		// 	this.$$("bell").config.badge = 0;
		// 	this.$$("bell").refresh();
		//
		// 	setTimeout(() => {
		// 		if (this.app){
		// 			this.$$("bell").config.badge += 1;
		// 			this.$$("bell").refresh();
		// 			this.app.callEvent("new:notification");
		// 		}
		// 	},10000);
		// });
	}

}
