import {JetView} from "webix-jet";
import {getServerBase, updateTitle} from "models/utils";

export default class VertretungsList extends JetView {
	config(){
		var format = webix.Date.dateToStr("%d.%m.%Y");
		return {
			gravity:2,
			rows:[
				{
					cols:[
						{type:"header", id: "listtitle", template:"Geplante Vertretungen", gravity:5},
						{
							view: "menu",
							id: "zeitbereich",
							align: "bottom, right",
							gravity:10,
							data:[
								{ value: "heute", id: "heute"},
								{ value: "morgen", id: "morgen"},
								{ value: "diese Woche", id: "woche"},
								{ value: "kommende Woche", id: "kommende"},
								{ value: "Neues", id: "neues"},
								{ value: "alle zuknftigen", id: "alle"},
								{ value: "ALLE", id: "all"},
							],
							on:{
								onItemClick:function(id){
									$$("datatable").clearAll();
									$$("datatable").url = getServerBase()+"/untis/vert/"+id;
									$$("datatable").load(getServerBase()+"/untis/vert/"+id);
									updateTitle(id, "listtitle");
								}
							}
						}

					]
				},
				{
					id:"datatable",
					view:"datatable",
					localId:"datatable",
					select:false,
					editable:false,
					tooltip:true,
					scroll:"y",
					url: getServerBase()+"/untis/vert/alle",
					on:{
						onBeforeLoad:function(){
							this.showOverlay("Daten aus Untis abrufen...");
						},
						onAfterLoad:function(){
							if (this.count() < 1) { // if there are no data items
								this.showOverlay("Keine Vertretungen gefunden.");
							}
							else{
								this.hideOverlay();
							}
						}
					},

					scheme:{
						$init:function(obj){
							if(obj.datum != null){
								obj.datumD = new Date(obj.datum);
								obj.lastChangeD = new Date(obj.lastChange);
								if(obj.vertRaum!=null && (obj.absRaum != obj.vertRaum)){
									if(obj.absRaum == null ){
										obj.absRaum = "???";
									}
									obj.raum = obj.absRaum + " -> " + obj.vertRaum;
								}
								else{
									obj.raum = obj.absRaum;
								}
							}
						}
					},

					columns:[
						{id:"datumD", header:"Tag", fillspace:3, sort:"date", tooltip:"",  format:format},
						{id:"stunde", header:"Std", fillspace:1, sort:"number", tooltip:""},
						{id:"vertLehrer", header:"Vert.", fillspace:2, sort:"string", tooltip:""},
						{id:"absLehrer", header:"war", fillspace:2, sort:"string", tooltip:""},
						{id:"absKlassen", header:"Klasse", fillspace:4, sort:"string", tooltip:""},
						{id:"raum", header:"Raum", fillspace:4, sort:"string", tooltip:""},
						{id:"vertArt", header:"Vert.art", fillspace:1, sort:"string", tooltip:"T: verlegt<br>" +
								"F: verlegt von<br>" +
								"W: Tausch<br>" +
								"S: Betreuung<br>" +
								"A: Sondereinsatz<br>" +
								"C: Entfall<br>" +
								"L: Freisetzung<br>" +
								"P: Teil-Vertretung<br>" +
								"R: Raumvertretung<br>" +
								"B: Pausenaufsichtsvertretung<br>" +
								"~: Lehrertausch<br>" +
								"E: Klausur"},
						{id:"absGrund", header:"Grund", fillspace:2, sort:"string", tooltip:""},
						{id:"vertText", header:"Text", fillspace:10, sort:"string", tooltip:""},
						{id:"lastChangeD", header:"eingetragen", fillspace:3, sort:"date", tooltip:"", format:format}
					]
				}
			]
		};
	}
	init(){
		$$("datatable").url = getServerBase()+"/untis/vert/heute";
		updateTitle("heute", "listtitle");
	}
}
