import {JetView} from "webix-jet";
// eslint-disable-next-line no-unused-vars
import {getServerBase} from "models/utils";


export default class Freisetzungsliste extends JetView {
	config() {

		var icnPDF = "<i class=\"pdf mdi mdi-24px mdi-file-pdf-outline\"></i>";
		var icnExcel = "<i class=\"excel mdi mdi-24px mdi-file-excel-outline\"></i>";
		var urlHeader = "http:./sources/public/Berufskolleg-Logo-080515.jpg";
		var format = webix.Date.dateToStr("%d.%m.%Y");

		return {
			rows: [
				{
					cols: [
						{
							type: "header",
							id: "listtitle",
							template: "Freisetzungen",
							gravity: 5
						},
						{
							view: "menu",
							id: "zeitbereich",
							align: "bottom, right",
							gravity: 10,
							data: [
								{value: "heute", id: "heute"},
								{value: "morgen", id: "morgen"},
								{value: "diese Woche", id: "woche"},
								{value: "kommende Woche", id: "kommende"},
								{value: "Neues", id: "neues"},
								{value: "alle zuknftigen", id: "alle"},
								{value: "ALLE", id: "all"},
							],
							on: {
								onItemClick: function (id) {
									$$("vertlist").clearAll();
									$$("vertlist").url = getServerBase() + "/untis/frei/" + id;
									$$("vertlist").load(getServerBase() + "/untis/frei/" + id);
								}
							}
						}

					]
				},
				{
					id: "vertlist",
					view: "datatable",
					localId: "vertlist",
					select: false,
					editable: false,
					tooltip: true,
					scroll: "y",
					url: getServerBase() + "/untis/frei/alle",
					on: {
						onBeforeLoad: function () {
							this.showOverlay("Daten aus Untis abrufen...");
						},
						onAfterLoad: function () {
							if (this.count() < 1) { // if there are no data items
								this.showOverlay("Keine Vertretungen gefunden.");
							} else {
								this.hideOverlay();
							}
						}
					},

					scheme: {
						$init: function (obj) {
							if (obj.datum != null) {
								obj.datumD = new Date(obj.datum);
								obj.lastChangeD = new Date(obj.lastChange);
								if (obj.vertRaum != null && (obj.absRaum != obj.vertRaum)) {
									if (obj.absRaum == null) {
										obj.absRaum = "???";
									}
									obj.raum = obj.absRaum + " -> " + obj.vertRaum;
								} else {
									obj.raum = obj.absRaum;
								}
							}
						}
					},

					columns: [
						{
							id: "datumD",
							header: "Tag",
							fillspace: 3,
							sort: "date",
							tooltip: "",
							format: format
						},
						{
							id: "stunde",
							header: "Std",
							fillspace: 1,
							sort: "number",
							tooltip: ""
						},
						{
							id: "vertLehrer",
							header: "Vert.",
							fillspace: 2,
							sort: "string",
							tooltip: ""
						},
						{
							id: "absLehrer",
							header: "war",
							fillspace: 2,
							sort: "string",
							tooltip: ""
						},
						{
							id: "absKlassen",
							header: "Klasse",
							fillspace: 4,
							sort: "string",
							tooltip: ""
						},
						{id: "raum", header: "Raum", fillspace: 4, sort: "string", tooltip: ""},
						{
							id: "vertArt",
							header: "Vert.art",
							fillspace: 1,
							sort: "string",
							tooltip: "T: verlegt<br>" +
								"F: verlegt von<br>" +
								"W: Tausch<br>" +
								"S: Betreuung<br>" +
								"A: Sondereinsatz<br>" +
								"C: Entfall<br>" +
								"L: Freisetzung<br>" +
								"P: Teil-Vertretung<br>" +
								"R: Raumvertretung<br>" +
								"B: Pausenaufsichtsvertretung<br>" +
								"~: Lehrertausch<br>" +
								"E: Klausur"
						},
						{
							id: "absGrund",
							header: "Grund",
							fillspace: 2,
							sort: "string",
							tooltip: ""
						},
						{
							id: "vertText",
							header: "Text",
							fillspace: 10,
							sort: "string",
							tooltip: ""
						},
						{
							id: "lastChangeD",
							header: "eingetragen",
							fillspace: 3,
							sort: "date",
							tooltip: "",
							format: format
						}
					]
				}
			]
		}
	}
}
