import {JetView} from "webix-jet";
// noinspection ES6UnusedImports
import {Config} from "models/utils";


export default class SendMailView extends JetView {
	config(){
		return {
			height: 400,
			gravity:2,
			rows:[
				{ type:"header", template:"Mailversand" },
				{ view:"toolbar", elements:[
					{ view:"button", value:"Send", width:100, click: "sendMail" }
				]},
				{ cols:[
					{view:"form", name: "mailForm", id: "mailForm", cols:[
						{
							rows:[
								{
									view:"text",
									width:400,
									label: "Betreff:",
//									value:"Neuer Vertretungsplan ab dem " + webix.i18n.dateFormatStr(new Date()),
									name:"mailBetreff",
									id: "mailBetreff"
								},
								{
									view:"calendar",
									id:"mailTag",
									name: "mailTag",
									label: "Datum",
									date:new Date(),
									weekHeader:true,
									width:400,
									height:250
								}
							]
						},
						{
							rows:[
								{
									view: "label", label:"Mailtext:"
								},
								{
//									container: "sample_div",
									view: "nic-editor",
									name: "mailText",
									id: "mailText",
									width:400,
									config: {
										buttonList: ["fontSize", "bold", "italic", "underline", "strikeThrough", "subscript", "superscript"],
										cdn: "./"
									},
									// value: `<p>Sehr geehrte Kolleginnen und Kollegen,</p><p>hier sind Ihre neuen Vertretungsplne.</p>
									// 			</p>Mit freundlichen Gren</br></br>Ihr Vertretungsteam</br>(GIL, KOE, TRA)<p>`
								}
							]
						}, //second column
						{
							type: "line",
							rows:[
								{ view:"label", id: "mailBetreffView", align:"left"},
								{
									view: "template",
									id: "mailTextView",
									height: 300,
									autoheight: true,
									borderless: false,
									align:"left",
									data: {src: ""},
									template: function(obj){
										return obj.src;
									}
								}
							]
						}
						]}
				]}
			]
		};
	}

	updateMailView(){
		var tag = $$("mailForm").getValues().mailTag;
		var tagStr = webix.i18n.dateFormatStr(tag);
		var betr = $$("mailForm").getValues().mailBetreff.replace("{tag}", tagStr);
		var text = $$("mailForm").getValues().mailText.replace("{tag}", tagStr);

		$$("mailBetreffView").setValue(betr);
		$$("mailTextView").setValues({src:text});
	}


	init() {
		var tag;
		var that = this;
		if(Config.mailTag === "heute"){
			tag = new Date();
		}
		else{
			var pts = Config.mailTag.split(".");
			tag = new Date(pts[2], pts[1]-1, pts[0]);
		}
		$$("mailTag").setValue(tag);
		$$("mailForm").elements["mailTag"].attachEvent("onChange", function(n, o){
			that.updateMailView();
		});
		$$("mailBetreff").setValue(Config.mailBetreff);
		$$("mailForm").elements["mailBetreff"].attachEvent("onChange", function(n, o){
			that.updateMailView();
		});
		$$("mailText").setValue(Config.mailText);
		// $$("mailForm").elements["mailText"].attachEvent("onChange", function(n, o){
		// 	updateMailView();
		// });
		this.updateMailView();
	}

	sendMail(){
		webix.confirm({
			title: "Send Mail",
			text: "Wirklich eine Mail an alle unten angekreuzten Eintrge schicken?",
			callback: function(result) {
				if (result) {
					var tagStr = $$("mailForm").getValues().mailTag;
					var betr = $$("mailForm").getValues().mailBetreff.replace("{tag}", tagStr);
					var text = $$("mailForm").getValues().mailText.replace("{tag}", tagStr);

				}

			}
		});
	}
}
