import {JetView} from "webix-jet";
import {getServerBase} from "models/utils";


export default class AggListe extends JetView {
	config(){
		if (webix.isUndefined($$("aggContent"))){
			webix.ui({
				view: "popup",
				id: "aggContent",
				hidden: true,
				height: 400, width: 1000,
				body:{
					view: "scrollview",
					id: "aggB",
					scroll: "xy", // vertical scrolling
					body: {
						rows:[
							{id:"aggc", template:"<b>fett</b> normal <i>kursiv</i>"}
						]
					}
				}
			}
			);
		}
		// uploader initialization
		if (webix.isUndefined($$("aggnUpload"))) {
			webix.ui({
				view: "uploader",
				id: "aggnUpload",
				apiOnly: true,
				multiple: false,
				upload: getServerBase() + "/analyse/gpn",
				on: {
					onBeforeFileAdd: function (item) {
						var type = item.type.toLowerCase(); //deriving file extension
						if (type != "gpn") { //checking the format
							webix.message("Nur Untis-GPN-Dateien!");
							return false;
						}
					},
					onUploadComplete: function(response){
						$$("anaListAgg").load(getServerBase() + "/analyse/aggList");
					},
				}
			});
		}



		return {
			gravity:2,
			cols: [
				{
					id:"anaListAgg",
					view:"datatable",
					localId:"anaListAgg",
					select:false,
					editable:false,
					tooltip:true,
					scroll:"y",
					url: getServerBase()+"/analyse/aggList",
					on:{
						onBeforeLoad:function(){
							this.showOverlay("Log abrufen...");
						},
						onAfterLoad:function(){
							if (this.count() < 1) { // if there are no data items
								this.showOverlay("Kein Log gefunden.");
							}
							else{
								this.hideOverlay();
							}
						},
						onViewShow: function () {
							$$("anaListAgg").refresh;
						}
					},

					scheme:{
						$init:function(obj){		// 2019-01-01T19:38:21.112
							if(obj!= null && "mehr" in obj && obj.mehr != null){
								obj.pprak = obj.mehr[0];
								obj.pkweg = obj.mehr[1];
								obj.plpweg = obj.mehr[2];
								obj.pldweg = obj.mehr[3];
							}
							if(obj!= null && "weniger" in obj && obj.weniger != null){
								obj.wprak = obj.weniger[0];
								obj.wkweg = obj.weniger[1];
								obj.wlpweg = obj.weniger[2];
								obj.wldweg = obj.weniger[3];
							}
						}
					},

					columns:[
						{id:"startOfWeek", header:"Woche", sort:"date", width: 140, format: webix.Date.dateToStr("%d.%m.%Y %H:%i"), tooltip:"Woche",adjustBatch: 10},
						{id:"lehrer", header:"KuK", sort:"string", width: 40, tooltip:"abwesender KuK"},

						{id:"pprak", header:"+Prak.", fillspace: true, tooltip:"Mehrarbeit: Klasse im Praktikum"},
						{id:"pkweg", header:"+Kl.abw.", fillspace: true, tooltip:"Mehrarbeit: Klasse abwesend"},
						{id:"plpweg", header:"+KuKpAbw.", fillspace: true, tooltip:"Mehrarbeit: KuK privat abwesend"},
						{id:"pldweg", header:"+KuKdAbw.", fillspace: true, tooltip:"Mehrarbeit: KuK dienstl. abwesend"},

						{id:"wprak", header:"-Prak.", fillspace: true, tooltip:"Freisetzung: Klasse im Praktikum"},
						{id:"wkweg", header:"-Kl.abw.", fillspace: true, tooltip:"Freisetzung: Klasse abwesend"},
						{id:"wlpweg", header:"-KuKpAbw.", fillspace: true, tooltip:"Freisetzung: KuK privat abwesend"},
						{id:"wldweg", header:"-KuKdAbw.", fillspace: true, tooltip:"Freisetzung: KuK dienstl. abwesend"},
					]
				},
			]
		};
	}

	init(){

	}
}
